.contacts {
    background-color: $secondary-bcg;
    margin-top: 170px;

    @media screen and (min-width: 768px) {
      margin-top: 197px;
    }

    @media screen and (min-width: 1280px) {
      margin-top: 234px;
      background-image: url(/images/contacts-bg.png);
      background-size: 1280px 382px;
      background-repeat: no-repeat;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/images/contacts-bg@2x.png);
      background-size: cover;
    }
    }
  }

.card {
  margin-bottom: -84px;

  @media screen and (min-width: 768px) {
    display: flex;
    margin: 0 -10px -78px;
  }

  @media screen and (min-width: 1280px) {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 -15px -102px;
  }
}

.contacts-items {
  position: relative;
  bottom: 84px;
    width: 220px;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
    border: 1px solid $vector-line;
    border-radius: 24px;
    background-color: #ffffff;
    box-shadow: 0px 8px 30px 0px #d414431a;
    padding: 43px 21px 43px 20px;
    margin-bottom: 20px;

    @media screen and (min-width: 768px) {
      bottom: 78px;
      padding: 50px 32px 50px 31px;
      margin: 0 10px 0;
    }

    @media screen and (min-width: 1280px) {
      bottom: 102px;
      width: 330px;
      padding: 51px 44px 62px 43px;
      margin: 0 15px 0;
    }

&__name,
&__name--green {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 16px;
    background-color: $title-product-first;
    color: $third-txt;
    text-align: center;
    border-radius: 4px;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.83;
    letter-spacing: 0.04em;
    margin-bottom: 25px;

    @media screen and (min-width: 768px) {
      width: 73px;
      height: 28px;
      font-size: 16px;
      line-height: 1.86;
      margin-bottom: 30px;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 32px;
    }
}

&__name--green {
    width: 130px;
    background-color: $title-product-second;

    @media screen and (min-width: 768px) {
      width: 138px;
    }
}
}

.place {
    position: relative;
    margin-bottom: 50px;

    @media screen and (min-width: 768px) {
      margin-bottom: 60px;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 64px;
    }

&::after {
    position: absolute;
    content: " ";
    bottom: -25px;
    left: 0;
    display: block;
    width: 100%;
    height: 1px;
    background-color: $vector-line;
}

&__title {
    color: $primary-txt;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.83;
    letter-spacing: 0.04em;
    text-align: left;
    margin-bottom: 10px;

    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.86;
      margin-bottom: 14px;
    }

    @media screen and (min-width: 1280px) {
      font-size: 16px;
      line-height: 1.86;
      margin-bottom: 10px;
    }
}

&__text {
    color: $secondary-txt;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.5;
    text-align: left;

    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.66;
    }

    @media screen and (min-width: 1280px) {
      font-size: 16px;
      line-height: 1.83;
    }
}
}

.work-time {
    position: relative;
    margin-bottom: 50px;

    @media screen and (min-width: 768px) {
      margin-bottom: 60px;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 64px;
    }


&::after {
    position: absolute;
    content: " ";
    bottom: -25px;
    left: 0;
    display: block;
    width: 100%;
    height: 1px;
    background-color: $vector-line;
}

&__item:first-child {
    margin-bottom: 10px;

    @media screen and (min-width: 768px) {
      margin-bottom: 16px;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 15px;
    }
}

&__days {
    color: $primary-txt;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.83;
    text-align: left;

    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.86;
    }

    @media screen and (min-width: 1280px) {
      font-size: 16px;
      line-height: 1.86;
    }
}

&__hours {
    color: $main-paragraf-txt;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.83;
    text-align: left;

    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.86;
    }

    @media screen and (min-width: 1280px) {
      font-size: 16px;
      line-height: 1.86;
    }
}
}

.list-connection__link {
    color: $secondary-txt;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.5;
    text-align: left;

    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.83;
    }

    @media screen and (min-width: 1280px) {
      font-size: 16px;
      line-height: 1.83;
    }
}

.contacts-button {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 54px;

    @media screen and (min-width: 768px) {
      flex-direction: row;
      margin-top: 81px;
    }

    @media screen and (min-width: 1280px) {
      margin-top: 86px;
    }


&__btn--red,
&__btn--white {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #d41342;
  color: $third-txt;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  text-align: center;
  width: 161px;
  height: 44px;
  border: none;
  border-radius: 22px;
  cursor: pointer;

  @media screen and (min-width: 768px) {
    width: 191px;
  }

  @media screen and (min-width: 1280px) {
    width: 198px;
  }
}

&__btn--white {
    background-color: $infocus-btn;
    color: $autofocus-btn;
    margin-top: 10px;

    @media screen and (min-width: 768px) {
      margin-top: 0;
      margin-left: 15px;
    }
}

&__btn--white:hover,
&__btn--white:focus {
  background-color: $focus-second-btn;
}

&__icon--white {
  stroke: #ffffff;
  margin-left: 8px;

  @media screen and (min-width: 768px) {
    margin-left: 13px;
  }
}

&__icon--red {
  stroke: $focus-icon-media;
  margin-left: 8px;

  @media screen and (min-width: 768px) {
    margin-left: 13px;
  }
}
}